<template>
  <div class="roles">
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>

    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色 -->
      <el-button type="primary" @click="addDialogVisible=true">添加角色</el-button>
      <!-- 角色列表区域 -->
      <el-table :data="roleList" border>
        <!-- 折叠列 -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row class="vcenter boder_botm" v-for="item1 in scope.row.children" :key="item1.id">
              <!-- 渲染一级权限 -->
              <el-col :span="5">
                <el-tag closable @close="removeRight(scope.row, item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!-- 渲染二级和三级权限 -->
              <el-col :span="19">
                <el-row class="vcenter boder_botm" v-for="item2 in item1.children" :key="item2.id">
                  <el-col :span="6">
                    <el-tag type="success" closable @close="removeRight(scope.row, item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning" closable @close="removeRight(scope.row, item3.id)" v-for="item3 in item2.children" :key="item3.id">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <!-- 索引列 -->
        <el-table-column type="index"></el-table-column>
        <el-table-column label="角色名称" prop="roleName"></el-table-column>
        <el-table-column label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column label="操作" width="300px">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" icon="el-icon-edit" @click="showEditDialog(scope.row.id)">编辑</el-button>
            <el-button size="mini" type="danger" icon="el-icon-delete" @click="deleteRole(scope.row.id)">删除</el-button>
            <el-button size="mini" type="warning" icon="el-icon-setting" @click="showSetRightDialog(scope.row)">分配权限</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!------------------------------------------------分割线------------------------------------------------>

    <!-- 添加角色的对话框 -->
    <el-dialog title="添加角色" :visible.sync="addDialogVisible" @close="closeAddDialog" :close-on-click-modal="false">
      <!-- 内容主体区域 -->
      <el-form :model="addRoleForm" :rules="addRules" ref="addRef" label-width="90px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="addRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="addRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeAddDialog">取 消</el-button>
        <el-button type="primary" @click="addRole">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 编辑角色的对话框 -->
    <el-dialog title="编辑角色" :visible.sync="editDialogVisible" @close="closeEditDialog" :close-on-click-modal="false">
      <!-- 内容主体区域 -->
      <el-form :model="editRoleForm" :rules="editRules" ref="editRef" label-width="90px">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="editRoleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="editRoleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!-- 底部区域 -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeEditDialog">取 消</el-button>
        <el-button type="primary" @click="editRole">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 分配权限对话框 -->
    <el-dialog title="分配权限" :visible.sync="setRightDialogVisible" @close="closeSetRightDialog">
      <!-- 树形控件 -->
      <el-tree :data="rightsList" :props="treeProps" show-checkbox node-key="id" default-expand-all :default-checked-keys="defKeys" ref="treeRef"></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="setRight">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  /* eslint-disable */
  name: 'Roles',
  data () {
    return {
      // 角色列表数据
      roleList: [],
      // 添加角色对话框-开关
      addDialogVisible: false,
      // 添加角色数据
      addRoleForm: {},
      // 添加角色数据校验
      addRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          {
            min: 1,
            max: 10,
            message: '角色名称的长度在3~10个字符之间',
            trigger: 'blur'
          }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          {
            min: 1,
            max: 50,
            message: '角色描述长度在50个字符以内',
            trigger: 'blur'
          }
        ],
      },
      // 编辑角色对话框-开关
      editDialogVisible: false,
      // 编辑角色表单数据
      editRoleForm: {},
      // 编辑角色数据校验
      editRules: {
        roleName: [
          { required: true, message: '请输入角色名称', trigger: 'blur' },
          {
            min: 1,
            max: 10,
            message: '角色名称的长度在3~10个字符之间',
            trigger: 'blur'
          }
        ],
        roleDesc: [
          { required: true, message: '请输入角色描述', trigger: 'blur' },
          {
            min: 1,
            max: 50,
            message: '角色描述长度在50个字符以内',
            trigger: 'blur'
          }
        ],
      },
      // 分配权限对话框-开关
      setRightDialogVisible: false,
      // 所有权限数据
      rightsList: [],
      // 树形控件的属性绑定对象
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // 默认选中的节点Id值数组
      defKeys: [],
      // 当前即将分配权限的角色id
      roleId: ''
    }
  },
  created() {
    // 获取角色列表数据
    this.getRolesList()
  },
  methods: {
    // 获取所有角色的列表
    async getRolesList() {
      const { data: res } = await this.axios.get('roles')
      if (res.meta.status !== 200) return this.$message.error('获取角色列表失败！')
      console.log(res);
      this.roleList = res.data
    },
    // 删除角色权限
    async removeRight(role, rightId) {
      // 弹框提示用户是否要删除
      const result = await this.$confirm( '此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (result !== 'confirm') return this.$message.info('取消了删除！')
      const { data: res } = await this.axios.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status !== 200) return this.$message.error('删除权限失败！')
      role.children = res.data
    },
    // 关闭添加角色对话框并清空数据
    closeAddDialog () {
      this.$refs.addRef.resetFields()
      this.addDialogVisible = false
    },
    // 添加角色确认
    addRole () {
      this.$refs.addRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.axios.post('roles', this.addRoleForm)
        if (res.meta.status !== 201) return this.$message.error(res.meta.msg)
        this.$message.success(res.meta.msg)
        this.getRolesList()
        this.addDialogVisible = false
      })
    },
    // 打开编辑对话框并渲染数据
    async showEditDialog (id) {
      const { data: res } = await this.axios.get('roles/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      console.log(res);
      this.editRoleForm = res.data
      this.editDialogVisible = true
    },
    // 关闭编辑角色对话框并清空数据
    closeEditDialog () {
      this.editRoleForm = {}
      this.editDialogVisible = false
    },
    // 编辑角色确认
    editRole () {
      this.$refs.editRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.axios.put(`roles/${this.editRoleForm.roleId}`, {
          roleName: this.editRoleForm.roleName,
          roleDesc: this.editRoleForm.roleDesc
        })
        if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
        this.$message.success('编辑角色成功！')
        this.editDialogVisible = false
        this.getRolesList()
      })
    },
    // 删除角色
    async deleteRole (id) {
      const result = await this.$confirm( '此操作将永久删除该角色, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      // 取消
      if (result !== 'confirm') {
        return this.$message.info('已取消删除')
      }
      // 确认发起删除请求
      const { data: res } = await this.axios.delete('roles/' + id)
      if (res.meta.status !== 200) return this.$message.error(res.meta.msg)
      this.$message.success('删除角色成功！')
      this.getRolesList()
    },
    // 打开分配权限对话框并渲染数据
    async showSetRightDialog(role) {
      this.roleId = role.id
      const { data: res } = await this.axios.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('获取权限数据失败！')
      // 把获取到的权限数据保存到 data 中
      this.rightsList = res.data
      // 递归获取三级节点的Id
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
    },
    // 通过递归的形式，获取角色下所有三级权限的id，并保存到 defKeys 数组中
    getLeafKeys(node, arr) {
      // 如果当前 node 节点不包含 children 属性，则是三级节点
      if (!node.children) {
        return arr.push(node.id)
      }

      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    // 关闭分配权限对话框
    closeSetRightDialog () {
      this.defKeys = []
    },
    // 分配权限
    async setRight() {
      // 获取各节点被选中id
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')

      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, {
        rids: idStr
      })
      if (res.meta.status !== 200) return this.$message.error('分配权限失败！')
      this.$message.success('分配权限成功！')
      this.getRolesList()
      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}
.vcenter {
  display: flex;
  align-items: center;
}
.boder_botm {
  border-bottom: 1px solid #eee;
}
.boder_botm:last-child {
  border-bottom: none
}
</style>
